import { Col, Row } from "antd";
import { useCallback, useEffect, useState } from "react";
import { LogCliService } from "../../../services/adminService";
import { Box, MyBox } from "../../core/MyBox";
import { MyButton } from "../../core/MyButton";

export default function LogStoredComponent() {
    const [isLoading, setIsLoading] = useState(false);
    const [timeMin, setTimeMin] = useState(0);
    const [timeMax, setTimeMax] = useState(0);
    const [timeAvg, setTimeAvg] = useState(0);
    const [data, setData] = useState([]);

    const loadData = useCallback(() => {
        setIsLoading(true);
        LogCliService.GetLogStored((data: any) => {
            console.log(data);
            const row = data.data.data;
            setTimeMin(row.timeMin);
            setTimeMax(row.timeMax);
            setTimeAvg(row.timeAverage);
            setData(row.data);
        }, undefined, () => {
            setIsLoading(false);
        });
    }, [])

    useEffect(() => {
        loadData();
    }, [loadData])

    function handleReload() {
        loadData();
    }

    function renderData() {
        return data.map((data: any, index: number) => {
            return (
                <Box key={index}>
                    <ul>
                        <li>Thời gian: {data.date}</li>
                        <li>Thời gian: {data.sql}</li>
                        <li>Thời gian: {data.times}</li>
                    </ul>
                </Box>
            )
        })
    }

    return (
        <MyBox
            title="Log stored procedure"
            isLoading={isLoading}
            buttonList={() => (
                <MyButton type="success" onClick={handleReload} value="Load Data" />
            )}>
            <Row gutter={24}>
                <Col span={8}>
                    <Box>
                        <p style={{ textAlign: "center" }}>Thời gian chậm nhất</p>
                        <h2 style={{ textAlign: "center" }}>{timeMax}</h2>
                    </Box>
                </Col>

                <Col span={8}>
                    <Box>
                        <p style={{ textAlign: "center" }}>Thời gian nhanh nhất</p>
                        <h2 style={{ textAlign: "center" }}>{timeMin}</h2>
                    </Box>
                </Col>

                <Col span={8}>
                    <Box>
                        <p style={{ textAlign: "center" }}>Thời gian trung bình: </p>
                        <h2 style={{ textAlign: "center" }}>{timeAvg}</h2>
                    </Box>
                </Col>
                <Col span={8}></Col>
                <Col span={8}></Col>
            </Row>
            {renderData()}
        </MyBox>
    );
}